import React, {createRef} from 'react';
import BaseSearch from '../BaseSearch';
import {Container, Input as StyledInput, InputContainer, InputWrapper, Item, Select as StyledSelect} from './newstyles'
import {Loader} from '../newstyles'
import Search from "./Search";
import LoadImage from '../../assets/loader.svg';

class RemoteSearch extends BaseSearch
{
  constructor(props)
  {
    super(props);

    this.state = {
      error: null,
      select: false,
      focused: false,
      hasError: false,
      search: props.search ? props.search : ''
    }

    this.wrapperRef = createRef();
    this.handleClickOutside = this.handleClickOutside.bind(this);
  }

  handleArrows()
  {
    if(this.state.handleArrow === false)
    {
      Search.func = (event) =>
      {
        const { items, selected, search, handle } = this.props;

        let resItems = items ? items
          .filter((item) => {

            if(search && search.length)
            {
              let parts = search.split(' ');
              let partsLength = parts.length;
              let partsFound = 0;

              for(let i = 0; i < partsLength; i++)
              {
                if(parts[i].replace('ё', 'е').replace('й', 'и').length > 0 && item?.name?.toLowerCase().replace('ё', 'е').replace('й', 'и').indexOf(parts[i].replace('ё', 'е').replace('й', 'и')) !== -1)
                {
                  partsFound++;
                }
              }

              if(partsFound > 0)
              {
                return true;
              }else{
                return false;
              }

            }else{
              return true;
            }
          })
          .filter((item) => {
            if(selected)
            {
              if(selected.id === item.id)
              {
                return  false;
              }
            }
            return  true;
          }) : [];

        // if (eventPrevented)
        // {
        //   return; // Do nothing if the event was already processed
        // }

        switch (event.key)
        {
          case "Down": // IE/Edge specific value
          case "ArrowDown":
            if(this.state.hovered === false)
            {
              this.setState({
                hovered: 0
              });
            }else{
              let length = resItems.length;
              let next = this.state.hovered + 1;

              if(next <= (length - 1))
              {
                this.setState({
                  hovered: next
                });
              }
            }

            // Do something for "down arrow" key press.
            break;
          case "Up": // IE/Edge specific value
          case "ArrowUp":
            let prev = this.state.hovered + -1;

            if(prev >= 0)
            {
              this.setState({
                hovered: prev
              });
            }
            break;
          case "Enter":
            if(this.state.hovered !== false)
            {
              let item = resItems[this.state.hovered];

              handle(item);
              this.handleShowSelect(false);
              this.stopHandleArrows();
              this.setState({
                search: item.name,
                selected: item
              });
            }


            event.preventDefault();
            break;
          default:
            return; // Quit when this doesn't handle the key event.
        }
      }

      window.addEventListener("keydown", Search.func, true);

      this.setState({
        handleArrow: true
      });
    }
  }

  handleShowSelect(bool)
  {
    this.setState({
      select: bool,
      focused: bool
    }, () => {
      if(this.state.select)
      {
        this.handleArrows();
      }else{
        this.stopHandleArrows();
      }
    });
  }

  onSearch(search)
  {
    if(typeof this.props.onSearch === 'function')
    {
      this.props.onSearch(search);
    }
  }


  onKeyPress(e)
  {
    if (typeof this.props.onKeyPress === 'function')
    {
      this.props.onKeyPress(e);
    }
  }

  stopHandleArrows()
  {
    window.removeEventListener('keydown', Search.func, true);

    this.setState({
      handleArrow: false
    });
  }

  render()
  {
    const { items, handle, name, loading, selected} = this.props;

    let search = this.state.search ? this.state.search.toLowerCase() : '';

    let resItems = items ? items
      .filter((item) => {
        if(search && search.length)
        {
          let parts = search.split(' ');
          let partsLength = parts.length;
          let partsFound = 0;

          for(let i = 0; i < partsLength; i++)
          {
            if(parts[i].replace('ё', 'е').replace('й', 'и').length > 0 && item?.name?.toLowerCase().replace('ё', 'е').replace('й', 'и').indexOf(parts[i].replace('ё', 'е').replace('й', 'и')) !== -1)
            {
              partsFound++;
            }
          }

          if(partsFound > 0)
          {
            return true;
          }else{
            return false;
          }

        }else{
          return true;
        }
      })
      // .filter((item) => {
      //   if(selected)
      //   {
      //     if(selected.id === item.id)
      //     {
      //       return  false;
      //     }
      //   }
      //   return  true;
      // })
      .map((item, key) => {
        return <Item key={item.id + (item.type_id ? item.type_id : '')} className={this.props.className + ' item ' + (this.state.hovered === key ? 'hovered' : '')} id={this.props.id + '-' + item.id} onClick={(e) => {
          e.stopPropagation();

          handle(item);
          this.handleShowSelect(false);
          this.stopHandleArrows();
          this.setState({
            search: item.name,
            selected: item
          });
        }}>
          <span>{item.name}</span>
        </Item>
      }) : [];

    return <Container dataid='remote-search' style={this.getContainerStyle()} className={this.props.className + (this.props.disabled ? ' disabled' : '')}>
      <InputWrapper className={this.getWrapperClasses(resItems)} ref={this.wrapperRef}>
        <InputContainer>
          <StyledInput
            selected={selected ? JSON.stringify(selected) : ''}
            id={this.props.id}
            autoComplete={'off'}
            disabled={this.props.disabled}
            style={this.props.style}
            className={this.props.className}
            type={this.props.type}
            name={this.getName(name)}
            value={this.state.search}
            placeholder={this.props.placeholder}
            onClick={() =>
            {
              this.handleShowSelect(true)
            }}
            onKeyPress={(e) =>
            {
              this.onKeyPress(e);
            }}
            onChange={(e) => {
              this.setState({
                search: e.target.value,
                hasError: false
              },() => {
                this.onSearch(this.state.search);
              });
            }}
            onFocus={() => {
              this.setState({
                focused: true,
                hasError: false
              })
            }}
          />
          {this.renderPlaceholder()}
          <StyledSelect id={this.props.id + '-select'} className={this.props.className + ' select'} select={this.state.select || this.state.focused}>
            {resItems.length ? resItems : (loading || selected ? '' : <Item className={this.props.className}><span>{typeof this.state.search === 'string' && this.state.search.length > 0 ? 'Ничего не найдено' : 'Ввидете запрос'}</span></Item>)}
          </StyledSelect>
          {this.renderTooltipError()}
          {loading && <Loader id='loader-for-data'>
            <div onClick={() => {}}>
              <img src={LoadImage} alt="Loading" width="20" height="20" />
            </div>
          </Loader>}
        </InputContainer>
      </InputWrapper>
    </Container>
  }
}


export default RemoteSearch

export {RemoteSearch}
